import Vue from 'vue';
import Vuex from 'vuex';


Vue.use(Vuex);
const store = new Vuex.Store({
    state:{

        isShowMoreMenus:false, // actionsheet菜单默认不显示
        isLogined:false, // 登录状态 默认未登录
        loginname:'', // 登录名称
        author_id:'', // 登录用户id
        accesstoken:'', // accesstoken码

      unLoginedMoreMenus:{
        login:'登录' // 用户未登陆的底部提示
      },

      isLoginedMoreMenus:{
        create:'发表新话题',
        center:'个人中心', // 用户已登录的底部提示
        msg:'消息通知',
        logout:'退出登录',
      },

      topics:[
        {name:'all',desc:'全部'}, // 导航栏
        {name:'good',desc:'精华'},
        {name:'share',desc:'分享'},
        {name:'ask',desc:'问答'},
        {name:'job',desc:'招聘'},
        {name:'test',desc:'测试'},
      ]
    },
    getters:{
        moreMenus(state){
            // 参数表示当前store对象的state
            if(state.isLogined){ // 条件判断 处于登陆/未登录状态 底部提示不同
              return state.isLoginedMoreMenus;
            }
            return state.unLoginedMoreMenus;
        },
        getTopicDescByTab:function(state){
          // 需要返回一个函数结构
          return function(tab){
            for (var index = 0;index<state.topics.length;index++){
              if(state.topics[index].name==tab){
                return state.topics[index].desc;
              }
            }
          }
        }
    },
    mutations:{
        // state数据必须放在mutation函数中统一修改
        mutationIsShowMoreMenus:function(state,payload){
            state.isShowMoreMenus=payload;
            
        },
        // 修改登陆状态
        mutationIsLogined:function(state){
          state.isLogined=true;
        },
        mutationLoginname:function(state,payload){
          state.loginname=payload;
        },
        // 修改accesstoken
        mutationAccesstoken:function(state,payload){
          state.accesstoken=payload;
        },
        // 用户注销
        mutationLogout:function(state){
          state.isLogined = false;
          state.loginname = '';
          state.accesstoken = '';
        },
        // 修改author_id
        mutationAuthorId:function(state,payload){
          state.author_id = payload;
        }
    },
    actions:{}
})


export default store;


