
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import { mapState } from 'vuex';
export default {
props:['name'],
components:{
Cell,Group,Scroller,Tab,TabItem
},
computed:{
...mapState(['topics'])
},
methods: {

getDesByTab(tab){
return this.$store.getters.getTopicDescByTab(tab);
},

routeto(index){

this.$router.push('/topiclist/'+this.topics[index].name);
},
freshData(){

var app=this;
this.$http.get('/topics',{
params:{
tab:this.name,
page:1,
limit:this.limit
}
}).then(function(res){

app.lists=res.data.data;



app.$refs.pullup.donePulldown();
})
},
loadMoreData(){

this.page=this.page+1;
var app=this;
this.$http.get('/topics',{
params:{
tab:this.name,
page:this.page,
limit:this.limit
}
}).then(function(res){

for(var i=0;i<res.data.data.length;i++){

app.lists.push(res.data.data[i]);
}



app.$refs.pullup.donePullup();
})
}
},
data:function(){
return{
lists:[],
page:1, // 当前页数 默认显示第一页
limit:5, // 每页显示的条数
pullupConfig:{  // 上划加载更多配置信息
content:'上划以获取更多数据',
downContent:'释放进行加载',
upContent:'加载更多',
loadingContent:'加载中...'
},
pulldownConfig:{  // 下拉加载更多配置信息
content:'下拉以获取更多数据',
downContent:'释放进行加载',
upContent:'加载更多',
loadingContent:'加载中...'
}

}
},



beforeRouteEnter (to, from, next) {




next(function(vm){

vm.$http.get('/topics',{
params:{
tab:to.params.name,
page:vm.page,
limit:vm.limit
}
}).then(function(res){

if(res.data.success){
vm.lists=res.data.data;
}
})
})
},
beforeRouteUpdate:function(to,from,next){
var app=this;
this.$http.get('/topics',{
params:{
tab:to.params.name,
page:this.page,
limit:this.limit
}
}).then(function(res){

if(res.data.success){
app.lists=res.data.data;
}
})

}
}



