
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {mavonEditor} from 'mavon-editor';
import 'mavon-editor/dist/css/index.css';
import axios from 'axios';
export default {
name:'create',
data(){
return{
title:'',
content:'',

toolbars:{
bold:true, // 粗体
italic:true, // 斜体
header:true, // 标题
underline:true, // 下划线
imagelink: true
}
}
},
methods: {

uploadImage:function(pos,file){
var app = this;





var params = new FormData();
params.append('smfile',file);
axios.post('https://sm.ms/api/upload',params,{




headers:{
'Content-Type':'multipart/form-data'
}
}).then(function(res){


app.$refs.md.$img2Url(pos,res.data.data.url)


})


},

addtopic:function(){

var app = this;

this.$http.post('/topics',{
title:this.title,
content:this.content,
tab:'dev',
accesstoken: // 来自于状态数据
this.$store.state.accesstoken
}).then(function(res){

if(res.data && res.data.success){

app.$router.replace('/topiclist/dev');
}
});
}
},
components:{
Group,XTextarea,XButton,mavonEditor
}
}



