
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

import { mapState, mapGetters,mapMutations, } from 'vuex';
import VmBackTop from 'vue-multiple-back-top';
export default {
name: 'app',
data:function(){
return{
badgeText:''
}
},
components:{
XHeader,
Actionsheet,
VmBackTop,
Badge
},

created:function() {

var loginStatus=window.localStorage.getItem('loginStatus');


if(loginStatus){

this.mutationIsLogined();
this.mutationLoginname(window.localStorage.getItem('mutationLoginname'));
this.mutationAccesstoken(window.localStorage.getItem('accesstoken'));
this.mutationAuthorId(window.localStorage.getItem('author_id'));
}

if(this.isLogined){

this.getUnreadMsgCount

var app = this;
window.setInterval(function(){
app.getUnreadMsgCount();
},5*60*1000);
}
},

methods: {

getUnreadMsgCount:function(){
var app = this;
this.$http.get('/message/count',{
params:{
accesstoken:
this.$store.state.accesstoken
}
}).then(function(res){

if(res.data.success){
app.badgeText = res.data.data;
}
})
},
...mapMutations(['mutationIsLogined',
'mutationLoginname',
'mutationAccesstoken',
'mutationAuthorId']),


routetohome:function(){
this.$router.replace('/');
},


ShowMoreMenus(){
this.isShowMoreMenus = true;
},
handleClickMoreMenu(menukey,menuitem){
this.$router.push('/'+menukey);
}
},



computed:{
...mapGetters(['moreMenus']),
...mapState([
'isLogined',
'unLoginedMoreMenus',
'isLoginedMoreMenus',
'topics'
]),


isShowMoreMenus:{


get:function(){
return this.$store.state.isShowMoreMenus
},

set:function(nval){
this.$store.commit('mutationIsShowMoreMenus',nval)
}
},

},

}



